import * as ajax from "@/utils/apiList";
export default {
  data() {
    return {
      options: [{
        value: 'car',
        label: '酷炫跑车'
      }, {
        value: 'animal',
        label: '可爱动物'
      }, {
        value: 'beauty',
        label: '美女图片'
      }, {
        value: 'comic',
        label: '动漫图片'
      }, {
        value: 'food',
        label: '美食图片'
      }, {
        value: 'game',
        label: '游戏图片'
      }, {
        value: 'movie',
        label: '影视图片'
      }, {
        value: 'person',
        label: '人物图片'
      }, {
        value: 'phone',
        label: '手机壁纸'
      }, {
        value: 'scenery',
        label: '自然风景'
      }],
      options_page: [{
        value: '1',
        label: '1'
      }, {
        value: '2',
        label: '2'
      }, {
        value: '3',
        label: '3'
      }, {
        value: '4',
        label: '4'
      }],
      value1: [],
      value2: [],
      data: [],
      type: 'food',
      total: '',
      page: 1,
      currentPage: 1,
      page_size: 18
    };
  },

  methods: {
    async getData() {
      let res = await ajax.getImages({
        type: this.type,
        page: this.page,
        size: this.page_size
      }); // const { data: { result: { list,total} } } = res;

      const {
        list,
        total
      } = res;
      this.total = total;
      this.data = list;
      this.value1 = [];
      this.value2 = [];
    },

    change(val) {
      this.type = val;
    },

    pages(v) {
      this.page = v;
    },

    checks(urls) {
      this.$confirm('跳转浏览至该图片, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '跳转成功!'
        });
        window.open(urls + '', '_blank');
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },

    // 分页
    handleSizeChange(val) {
      this.page_size = val;
      this.getData();
    },

    //选择每页数量
    handleCurrentChange(val) {
      this.page = val;
      this.getData();
    }

  },

  mounted() {
    this.getData();
  }

};